/** @private */
export function getTwitchApiUrl(url, type) {
  switch (type) {
    case 'helix':
      return "https://api.twitch.tv/helix/".concat(url.replace(/^\//, ''));

    case 'auth':
      return "https://id.twitch.tv/oauth2/".concat(url.replace(/^\//, ''));

    case 'custom':
      return url;

    default:
      return url;
    // wat
  }
}